.container-contact {
  justify-content: space-around;
  padding: 20px 40px;
}

.container-form {
  max-width: 100%;
  width: auto;
  // width: 50vw;
  height: auto;
  // margin: 20px;
  margin: 20px auto;
  padding: 20px;
  justify-content: left;
  display: flex;
  flex-direction: column;
  border: gray 1px solid;
  border-radius: 20px;
  @include onTablet {
      width: 80vw;
      height: auto;
      // margin: 20px;
      margin: 20px auto;
      padding: 20px;
    }
}

.container-form h2{
  display: flex;
  justify-content: center;
  align-items: center;
}

.contact-form {
  width: 100%;
  padding: 10px;
}

.form-group {
  margin-bottom: 20px;

  label {
    margin-bottom: 5px;
    font-weight: 200px;
    color: #ffffff;
  }

  input[type="text"],
  input[type="tel"],
  input[type="email"],
  textarea {
    width: 100%;
    padding: 10px;
    border: 1px solid #ffffff;
    background: transparent;
    color: white;
  }
}

.btn {
  width: 100%;
}

.btn:hover {
  transform: scale(1.05);
}

.social-media {


  .social-link {
    display: flex;
    margin-bottom: 10px;
    text-decoration: none;
    color: #007bff;

    &:hover {
      text-decoration: underline;
    }
  }
}

iframe {
  width: 50vw;
  height: 60vh;
  margin: 20px;
  @include onTablet {
    width: 80vw;
    height: 50vh;
  }
}
