*,*::before,*::after{
  padding: 0;
  margin: 0;
  border: 0;
  box-sizing: border-box;
}

html, body{
  color: white;
  background-color: black;
  height: 100%;
  font-family: 'Montserrat', sans-serif;
  font-size: 16px;
  line-height: 140%;
  scroll-behavior: smooth;
  box-sizing: border-box;
}

.body {
  @include onTablet {
    &.lock{
      overflow: hidden;
    }
  }
}

.wrapper {
  display: flex;
  flex-direction: column;
  min-height: 100vh;

}
