.social {
  display: flex;
  height: auto;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  padding: 0px 60px;
}

.container__social {
  flex-direction: column;
  display: flex;
  width: content;
  height: 100%;
  @include onTablet {
    flex-direction: row;
    padding: 10px;
  }
}

.container__social .link {
  display: inline-block;
  vertical-align: middle;
  position: relative;
  width: 90px;
  height: 90px;
  border-radius: 50%;
  border: 2px dashed white;
  padding: 10px;
  transition: .5s;
  color: #D7D0BE;
  margin-bottom: 10px;
  @include onTablet {
    width: 60px;
    height: 60px;
  }
}

.container__social .link:not(:last-child) {
  @include onTablet {
      margin: 0px 15px 0px 0px;
    }
}

.container__social .link:last-child {
  margin-bottom: 0px;
}

.container__social .link:hover {
  color: white;
  transform: rotate(360deg);
  transition: transform 0.5s ease, color 0.5s ease;
}

.container__tittle-social {
  display: flex;
  flex-direction: column;
  line-height: 140%;
  justify-content: flex-start;
  justify-content: space-around;
  height: 290px;
  padding-left: 50px;
  @include onTablet {
    height: 350px;
    font-size: 20px;
    padding: 0;
  }
}

.container__tittle-social a, p {
  text-decoration: none;
  font-size: 20px;
  color: orange;
}

.container__tittle-social a:hover {
  font-size: 22px;
  transition: 0.3s ease;
}
