#scroll {
  position: fixed;
  right: 20px;
  bottom: 20px;
  cursor: pointer;
  width: 50px;
  height: 50px;
  background-color: #db8a34;
  opacity: 0;
  -webkit-border-radius: 60px;
  -moz-border-radius: 60px;
  border-radius: 60px;
  z-index: 50;
  display: flex;
  align-items: center;
  justify-content: center;
}

.scroll-text {
  text-decoration: none;
  color: white;
  font-size: 14px;
  font-weight: bold;
}

.scroll-text:hover {
  color: black;
}
