.card {
  box-sizing: border-box;
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  background: rgb(20, 20, 20);
  margin: 20px;

  &__services,
  &__price {
    font-size: 18px;
    color: orange;
    display: block;
    text-align: left;
    padding: 10px;
    justify-content: center;
    align-items: center;
    list-style: none;
    line-height: 32px;
  }

  &__price {
    font-size: 28px;
    font-weight: bold;
  }

  &__btn {
    display: inline-block;
    width: 130px;
    height: 40px;
    line-height: 42px;
    color: black;
    border: none;
    border-radius: 5px;
    font-weight: 500;
    background: rgb(255, 204, 0);
    cursor: pointer;
    transition: background 0.5s ease, transform 0.5s ease, box-shadow 0.5s ease, color 1s ease;
    position: relative;
    outline: none;
    text-align: center;
    overflow: hidden;
    box-shadow: 2px 2px 10px orange;
  }

  &__btn:hover {
    color: white;
    background: rgb(255, 0, 0);
    font-size: 14px;
    transform: scale(1.2);
    box-shadow: inset 2px 2px 20px 0px rgb(248, 178, 0),
      2px 2px 10px 0px rgb(241, 165, 1),
      4px 4px 5px 0px rgba(0, 0, 0, 0.1);
  }
}

.card__btn::before,
.card__btn::after {
  content: '';
  position: absolute;
  top: 0;
  right: 0;
  height: 2px;
  width: 100%;
  background: black;
  transition: width 0.5s ease;
}

.card__btn::after {
  right: initial;
  left: 0;
  bottom: 0;
}

.card__btn:hover::before,
.card__btn:hover::after {
  width: 0;
}

@property --rotate {
  syntax: "<angle>";
  initial-value: 132deg;
  inherits: false;
}

