.card {
  // width: var(--card-width);
  // height: var(--card-height);
  width: auto;
  height: 400px;
  padding: 3px;
  position: relative;
  border-radius: 6px;
  align-items: center;
  color: rgba(38, 164, 223, 0);
  @include onTablet {
    height: 350px;
  }

  &::before {
    content: "";
    width: 102%;
    height: 101%;
    border-radius: 10px;
    background-image: linear-gradient(var(--rotate), rgb(255, 132, 0) 43%, black 73%, red);
    position: absolute;
    z-index: -1;
    animation: spin 10s linear infinite;
  }

  &::after {
    position: absolute;
    content: "";
    top: calc(var(--card-height) / 6);
    left: 0;
    right: 0;
    z-index: -1;
    height: 100%;
    width: 100%;
    margin: 0 auto;
    transform: scale(0.6);
    filter: blur(calc(var(--card-height) / 6));
    background-image: linear-gradient(var(--rotate), rgb(223, 124, 3) 43%, black, red);
    opacity: 1;
    transition: opacity .5s;
    animation: spin 2.5s linear infinite;
  }
}

@keyframes spin {
  0% {
    --rotate: 0deg;
  }

  100% {
    --rotate: 360deg;
  }
}
