

.container__advantages {
  box-sizing: border-box;
  padding-top: 40px;
  display: flex;
  width: 100%;
}

.advantages {
  border: 5px rgb(255, 255, 255) solid;
  display: flex;
  width: 100%;
  font-size: 10px;
}

.advantages__blocks {
  text-align: center;
  @include onTablet {
    font-size: 14px;
  }
  @include onMob {
    font-size: 10px;
  }
}

.advantages__blocks p {
  margin: 20px;
  @include onMob {
    font-size: 10px;
  }
}

.advantages__blocks img {
  padding-bottom: 15px;
}

