.sdf {
  display: flex;
  position: relative;
}

#str {
  position: absolute;
  width: 700px;
  height: auto;
  padding: 20px;
  font-family: sans-serif;
  font-size: 24px;
  top: 280px;
  color: #fff;
  font-weight: bold;
  line-height: 140%;
  @include onTablet {
    width: 500px;
    font-size: 20px;
    top: 200px;
  }
  @include onMob {
    width: 350px;
    font-size: 16px;
    top: 100px;
  }
}
