
$color_black: #000000;
$color_prime: #000000;
$color_grey: #e2e2e2;
$color_grey_dark: #a2a2a2;

.content__trener{
  width: 100%;
  height: 100vh;
  background: black;

}

.block-card--list {
  padding: 20px;
}

/* Стили для кнопки "Записаться" */
.signup-button {
  background-color: #ff9100;
  color: #fff;
  border: none;
  padding: 10px 50px;

  border-radius: 3px;
  cursor: pointer;
  font-weight: bold;
  text-transform: uppercase;

  &:hover {
    background-color: #ff6803;
    color: black;
  }
}

/* Стили для формы */
.signup-form {
  color: rgb(168, 168, 168);
  display: none;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  margin-top: 20px;

  h3 {
    font-size: 18px;
    margin-bottom: 10px;
  }
}

/* Стили для полей ввода */
.signup-form label {
  display: block;
  margin-bottom: 5px;
  font-weight: bold;
}

.signup-form input[type="text"],
.signup-form input[type="email"],
.signup-form input[type="tel"] {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
  border: 1px solid #ccc;
  border-radius: 3px;
}

.signup-form .submit-button {
  background-color: #ff9100;
  color: #fff;
  border: none;
  padding: 10px 20px;
  border-radius: 3px;
  cursor: pointer;
  font-weight: bold;

  &:hover {
    background-color: #ff6803;
  }
}

.blog-card {
  display: flex;
  flex-direction: column;
  margin: 1rem auto;
  box-shadow: 0 3px 7px -1px rgba(#000, .1);
  margin-bottom: 1.6%;
  background: $color_black;
  line-height: 1.4;
  font-family: sans-serif;
  border-radius: 5px;
  overflow: hidden;
  z-index: 0;

  a {
    color: inherit;

    &:hover {
      color: $color_prime;
    }
  }

  .meta {
    position: relative;
    z-index: 0;
    height: 200px;
  }

  .photo {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    background-size: cover;
    background-position: center;
    transition: transform .2s;
  }

  .photo-1{
    background-image: url(../images/abs-1850926_1280.jpg);
  }
  .photo-2 {
    background-image: url(../images/istockphoto-1152600445-1024x1024.jpg);
  }

  .photo-3 {
    background-image: url(../images/istockphoto-1434950927-1024x1024.jpg);
  }

  .photo-4 {
    background-image: url(../images/man-1282232_640.jpg);
  }

  .description {
    padding: 1rem;
    margin: 10px;
    background: $color_black;
    position: relative;
    z-index: 2;
  }

  p {
    position: relative;
    margin: 1rem 0 0;

    &:first-of-type {
      margin-top: 1.25rem;

      &:before {
        content: "";
        position: absolute;
        height: 5px;
        background: $color_prime;
        width: 35px;
        top: -0.75rem;
        border-radius: 3px;
      }
    }
  }

  @media (min-width: 640px) {
    flex-direction: row;
    max-width: 700px;

    .meta {
      flex-basis: 40%;
      height: auto;
      z-index: 0;
    }

    .description {
      flex-basis: 60%;
      z-index: 10;
    }
  }
}
