.content{
  flex: 1;
  box-sizing: border-box;
}

.content__front {
  width: 100%;
  height: auto;
  opacity: 90%;
  z-index: 1;
  animation: theme 12s linear infinite;
  &:after,
  &:before {
    content: "";
    display: block;
    position: fixed;
    z-index: -1;
    top: 0;
    width: 100vw;
    height: 100vh;
    background: linear-gradient(to bottom, #000000, #ffd00038);
    animation: background 90s linear infinite ;
  }

  &:after {
    left: 15vw;
  }

  &:before {
    right: 15vw;
    animation-delay: -30s;
    animation-direction: reverse;
  }
}
.content-img {
  width: 100%;
  height: auto;
}

.content__background--img {
  max-width: 100%;
  height: auto;
  z-index: 10;
  opacity: 100%;
}

@keyframes theme {
  0% {
    background: #000000;
  }

  10% {
    background: #2e2e2e;
  }

  20% {
    background: #545454;
  }

  30% {
    background: #7a7a7a;
  }

  40% {
    background: #a0a0a0;
  }

  50% {
    background: #c6c6c6;
  }

  60% {
    background: #a0a0a0;
  }

  70% {
    background: #7a7a7a;
  }

  80% {
    background: #545454;
  }

  90% {
    background: #2e2e2e;
  }

  100% {
    background: #000000;
  }
}

@keyframes background {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}


