.header {
  position: sticky;
  width: 100%;
  height: auto;
  padding: 0px 20px;
  top: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  align-items: center;

  .content__header {
    display: flex;
    align-items: center;
  }

  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: black;
    z-index: 2;
  }
  &__body {
    position: relative;
    display: flex;
    justify-content: space-between;
    height: 80px;
    align-items: center;
    // @include onTablet {
    //   height: 50px;
    //   padding: 0 10px;
    // }
  }

  &__logo {
    flex: 0 0 150px;
    overflow: hidden;
    z-index: 3;
    @include onTablet {
      flex: 0 0 120px;
      z-index: 0;
    }
    @include onMob {
      flex: 0 0 90px;
      z-index: 0;
    }
  }

    &__logo:hover{
      transform: scale(1.12);
      transition: 0.3s ease;
    }

  &__logo img{
    max-width: 100%;
    display: block;

  }

  &__burger {
    display: none;
    &.active::before {
      transform: rotate(45deg);
      top: 9px;
    }

    &.active::after {
      transform: rotate(-45deg);
      bottom: 9px;
    }
    &.active span {
      transform: scale(0);
    }

    @include onTablet {
      display: block;
      position: relative;
      width: 30px;
      height: 20px;
      z-index: 3;
    }
  }

  &__burger span {
    position: absolute;
    background-color: white;
    left: 0;
    width: 100%;
    height: 2px;
    top: 9px;
    transition: all 0.3s ease 0s;

  }

  &__burger::after,
  &__burger::before {
    content: "";
    background-color: white;
    position: absolute;
    width: 100%;
    height: 2px;
    left: 0;
    transition: all 0.3s ease 0s;
  }

  &__burger::after {
    bottom: 0;
  }
  &__burger::before {
    top: 0;
  }

  &__burger:hover::before {

  }

  &__menu {
    @include onTablet {
      position: fixed;
      top: -100%;
      left: 0;
      width: 100%;
      height: 100%;
      background-color: rgb(0, 0, 0);
      padding: 100px 10px 20px 10px;
      line-height: 40px;
      transition: all 0.4s ease 0s;
      overflow: auto;
    }
    &.active {
      top: 0;
    }
  }

  &__list {
    display: flex;
    position: relative;
    z-index: 2;
    @include onTablet {
      display: block;
    }
    &-item {
      text-align: center;
    }
    &-item:hover {
      border-radius: 5px;
      color: orange;
      transition: 0.3s ease-in-out;
    }
  }


  &__list li {
    list-style: none;
    margin: 0px 0px 0px 20px;
    @include onTablet {
      margin-bottom: 20px;
      text-align: center;
    }
  }

  &__link {
    text-decoration: none;
    color: white;
    position: relative;
    font-size: 24px;
    padding: 5px;
    @include onTablet {
      font-size: 32px;
    }
  }

  &__link:hover {
    padding: 5px;
      color: black;
      transition: 0.3s ease;
      border: orange 2px solid;
      background: orange;
      border-radius: 15px;
    @include onTablet {
      padding: 5px;
      color: white;
      transition: 0.3s ease;
      border: orange 2px solid;
      border-radius: 15px;
      background: orange;
      color: black;
      }
  }
}




